.mansonry {
    @apply relative columns-10 [&:has(figure:hover)_figure:hover_figcaption]:opacity-100 [&:has(figure:hover)_figure:hover_figcaption]:translate-y-0 gap-5;

    @media screen and (max-width:1720px) {column-count: 9; }
    @media screen and (max-width:1520px) {column-count: 8; }
    @media screen and (max-width:1420px) {column-count: 7; }
    @media screen and (max-width:1200px) {column-count: 5; }
    @media screen and (max-width:960px)  {column-count: 3; }
    @media screen and (max-width:640px)  {column-count: 2; }

    figure {
        @apply m-0 [grid-template-rows:1fr_auto] break-inside-avoid relative duration-500 transition-all cursor-pointer overflow-hidden grid p-0 mb-5;

        div {
            @apply overflow-hidden h-full w-full relative [grid-row:1/-1] [grid-column:1]
            after:content-[''] after:absolute after:bottom-0 after:left-0 after:w-full after:h-0 after:bg-[linear-gradient(to_top,#1119,#0000)]
            after:saturate-[3] after:duration-100 after:transition-all after:pointer-events-none after:z-50 hover:after:h-full;
        }
        figcaption {
            @apply absolute bottom-3 left-4 z-50 w-full h-full pointer-events-none px-4 py-5 duration-300 transition-all opacity-0 translate-y-8 [text-shadow:1px_0px_7px_#000a]
        }
        &:has(.loaded) {
            img{
              opacity: 1
            }
            .placeholder{
                opacity: 0;
            }
        }
    }
}

